	module.exports = "<h2 id=overview>Overview</h2> <p>Always add a modifier class to add color to the button.</p> <h2 id=button-vs-link>Button vs Link</h2> <p>Semantic buttons and links are important for usability as well as accessibility. Using an <code>a</code> instead of a <code>button</code> element to perform user initiated actions should be avoided, unless absolutely necessary.</p> <h2 id=accessibility>Accessibility</h2> <table> <thead> <tr> <th>Attribute</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>aria-pressed=&quot;true or false&quot;</code></td> <td><code>.pf-c-button</code></td> <td>Indicates that the button is a toggle. When set to &quot;true&quot;, <code>pf-m-active</code> should also be set so that the button displays in an active state. <strong>Required when button is a toggle</strong></td> </tr> <tr> <td><code>aria-label=&quot;[button label text]&quot;</code></td> <td><code>.pf-m-plain</code></td> <td>Provides an accessible name for the button when an icon is used instead of text. <strong>Required when icon is used with no supporting text</strong></td> </tr> <tr> <td><code>aria-label=&quot;[link description]&quot;</code></td> <td><code>a.pf-c-button</code></td> <td>The link text should adequately describe the link&#39;s purpose. If it does not, aria-label can provide more detailed interaction information.</td> </tr> <tr> <td><code>disabled</code></td> <td><code>button.pf-c-button</code></td> <td>When a button element is used, indicates that it is unavailable and removes it from keyboard focus. <strong>Required when button is disabled</strong></td> </tr> <tr> <td><code>aria-disabled=&quot;true&quot;</code></td> <td><code>a.pf-c-button</code></td> <td>When a link element is used, indicates that it is unavailable. <strong>Required when link is disabled</strong></td> </tr> <tr> <td><code>tabindex=&quot;-1&quot;</code></td> <td><code>a.pf-c-button</code></td> <td>When a link element is used, removes it from keyboard focus. <strong>Required when link is disabled</strong></td> </tr> </tbody> </table> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-c-button</code></td> <td><code>&lt;button&gt;</code></td> <td>Initiates a button. Always use it with a modifier class. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-c-button__icon</code></td> <td><code>*</code></td> <td>Applies styles to icons in a button. For use when there is an icon followed by text.</td> </tr> <tr> <td><code>.pf-m-primary</code></td> <td><code>.pf-c-button</code></td> <td>Modifies for primary styles.</td> </tr> <tr> <td><code>.pf-m-secondary</code></td> <td><code>.pf-c-button</code></td> <td>Modifies for secondary styles.</td> </tr> <tr> <td><code>.pf-m-tertiary</code></td> <td><code>.pf-c-button</code></td> <td>Modifies for tertiary styles.</td> </tr> <tr> <td><code>.pf-m-danger</code></td> <td><code>.pf-c-button</code></td> <td>Modifies for danger styles.</td> </tr> <tr> <td><code>.pf-m-link</code></td> <td><code>.pf-c-button</code></td> <td>Modifies for link styles. This button has no background or border and is styled as a link. This button would commonly appear in a form and may include an icon.</td> </tr> <tr> <td><code>.pf-m-plain</code></td> <td><code>.pf-c-button</code></td> <td>Modifies for icon styles. This button has no background or border, uses a standard text color, and is used for <code>.pf-m-plain</code> icon buttons such as close, expand, kebab, etc.</td> </tr> <tr> <td><code>.pf-m-block</code></td> <td><code>.pf-c-button</code></td> <td>Creates a block level button.</td> </tr> <tr> <td><code>.pf-m-hover</code></td> <td><code>.pf-c-button</code></td> <td>Forces display of the hover state of the button. This state is primarily for demonstration purposes and would not normally be used in lieu of the <code>:hover</code> pseudo-class.</td> </tr> <tr> <td><code>.pf-m-active</code></td> <td><code>.pf-c-button</code></td> <td>Forces display of the active state of the button. This state is primarily for demonstration purposes and would not normally be used in lieu of the <code>:active</code> pseudo-class.</td> </tr> <tr> <td><code>.pf-m-focus</code></td> <td><code>.pf-c-button</code></td> <td>Forces display of the focus state of the button. This state is primarily for demonstration purposes and would not normally be used in lieu of the <code>:focus</code> pseudo-class.</td> </tr> </tbody> </table> ";

